<template>
	<div class="popup-box-underlay" v-show="value">
		<div class="popup-box-underlay-inner">
			<slot></slot>
		</div>
	</div>
</template>

<script>
export default {
	name: 'PopupBoxUnderlay',
	props: {
		value: {
			default: true
		}
	}
}
</script>

<style>
.popup-box-underlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: hsla(0, 0%, 0%, 0.9);
	/*[TODO]*/
}
.popup-box-underlay-inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
	/*outline: 1px solid red;*/
	background: red;
	padding: 0;
	margin: 0;
}
</style>