<template>
	<div id="orientation-sensors">
		<section>
			<code>{{ status }}</code>
		</section>
		<section>
			<h5>Orientation</h5>
			<label>
				<span>α</span>
				<meter min="0" max="360" :value="orientation.α"></meter>
				<span v-text="orientation.α"></span>
			</label>
			<label>
				<span>β</span>
				<meter min="-180" max="180" :value="orientation.β"></meter>
				<span v-text="orientation.β"></span>
			</label>
			<label>
				<span>γ</span>
				<meter min="-90" max="90" :value="orientation.γ"></meter>
				<span v-text="orientation.γ"></span>
			</label>

			<label>
				<span></span>
				<code v-if="orientation.absolute === true">Absolute (Earth)</code>
				<code v-else-if="orientation.absolute === false">Relative</code>
				<code v-else>No data</code>
			</label>
		</section>

		<!-- <section>
			<h5>Acceleration</h5>	
		</section> -->
	</div>
</template>

<script>
export default {
	name: 'OrientationSensors',
	data() {
		return {
			status: 'Hmmm...',
			orientation: {
				absolute: undefined,
				α: undefined,	// perpendicular to z-axis, 0 to 360, where 0 is north
				β: undefined,	// perpendicular to x-axis, -180 to 180, where 0 is flat
				γ: undefined	// perpendicular to y-axis, -90 to 90, where 0 is flat
			},
		}
	},
	methods: {
		orientationListener(ev) {
			this.status = 'Fired'

			this.orientation.α = ev.alpha
			this.orientation.β = ev.beta
			this.orientation.γ = ev.gamma
			// Absolute means relative to earth
			this.orientation.absolute = ev.absolute
		},
		accelerationListener(ev) {

		},
		detectSupport() {
			if (window.location.protocol !== 'https:') {
				this.status = 'Page not served with https, so probably won\'t work...'
				return
			}

			let supported = []
			if ('ondeviceorientationabsolute' in window) {
				supported.push('ondeviceorientationabsolute')
			}
			if ('ondeviceorientation' in window) {
				supported.push('ondeviceorientation')
			}
			if ('ondevicemotion' in window) {
				supported.push('ondevicemotion')
			}

			this.status = 'Supported: ' + supported.join(', ')
		}
	},
	created() {
		this.detectSupport()

		window.addEventListener("deviceorientation", this.orientationListener)
		window.addEventListener("devicemotion", this.accelerationListener)
	},
	destroyed() {
		window.removeEventListener("deviceorientation", this.orientationListener)
		window.removeEventListener("devicemotion", this.accelerationListener)
	}
}
</script>

<style>
#orientation-sensors {
	
}
#orientation-sensors label {
	display: block;
}
#orientation-sensors label span {
	width: 3ch;
}
</style>