<template>
	<span>
		<button ref="theButton" @click="showPopup = !showPopup">
			<slot></slot>
		</button>

		<PopupBox type="fullscreen" :target="$refs.theButton" v-model="showPopup">
			<!-- <header>
				<span>PopupBox </span>
				<button @click="toggle">Close</button>
			</header> -->
			<slot name="PopupBox"></slot>
		</PopupBox>

	</span>
</template>

<script>
import PopupBox from './PopupBox'

export default {
	name: 'PopupButton',
	data() {
		return {
			showPopup: true
		}
	},
	components: {
		PopupBox
	}
}
</script>

