// Vue directive: ShowAWhile
// USAGE:
// 	<template v-show-a-while="2000"></template>
// WHERE:
// 	params: how long to show the element, in milliseconds

export default {
	inserted(el, binding) {
		let time = binding.value || 1000
		window.setTimeout(() => {
			el.style.display = 'none'
		}, time)
	}
}