<template>
	<button :disabled="text === undefined" @click="copy()">
		<slot>copy</slot>
	</button>
</template>

<script>
export default {
	name: 'CopyButton',
	props: {
		text: {
			default: ''
		}
	},
	computed: {
		computedText() {
			return (typeof this.text === 'string') ? this.text : JSON.stringify(this.text)
		}
	},
	methods: {
		copy() {
			// Create offscreen input element
			let $input = document.createElement('input')
			$input.type = 'text'
			$input.value = this.computedText
			$input.style = 'position: fixed; left: -1000px; top: -1000px; width: 0; height: 0; overflow: hidden;'
			document.body.appendChild($input)

			// Copy
			$input.select()
			document.execCommand("copy")

			// Remove input element
			document.body.removeChild($input)

			// Callback
			this.$emit('copy', this.computedText)
		}
	}
}
</script>

