<!-- 
	<PopupBox 
		type="fullscreen" 
		:target="$refs.theButton" 
		v-model="showPopup"
		clickOutsideToClose
	>
		Content
	</PopupBox>
 -->

<template>
	<transition name="popup-box">
		<div class="popup-box" v-show="value">
			<slot></slot>
		</div>
	</transition>
</template>

<script>
export default {
	name: 'PopupBox',
	props: {
		// Show or hide the menu, used for v-model
		value: {
			type: Boolean,
			default: true
		},
		type: {
			validator(type) {
				return ['fullscreen', 'cat'].indexOf(type) !== -1
			}
		},
		// Which button element triggered it. Required for clickOutsideToClose to work
		target: {
			validator(value) {
				return value instanceof HTMLElement
			}
		},
		// If true, clicking anywhere outside of the PopupBox will close the PopupBox
		clickOutsideToClose: {
			default: false
		}
	},
	computed: {
		// position() {
		// 	let x, y



		// 	return {
		// 		top: y,
		// 		left: x
		// 	}
		// },
	},
	methods: {
		close() {
			this.$emit('input', false)
		},
		clickOutsideToCloseFn(event) {
			// Return if already closed
			if (this.value === false) {
				return
			}

			// Return if anything inside trigger button is clicked
			if (this.target && this.target.contains(event.target)) {
				return
			}

			// If user clicked outside this component, close
			if (this.$el.contains(event.target) === false) {
				this.close()
			}
		}
	},
	destroyed() {
		if (this.target) {
			document.body.removeEventListener('click', this.clickOutsideToCloseFn)
		}
	},
	watch: {
		target(value) {
			if (value && this.clickOutsideToClose !== undefined) {
				// Attach global events
				document.body.addEventListener('click', this.clickOutsideToCloseFn)
			}
		}
	}
}
</script>

<style>
.popup-box {
	/* Positioning */
	/*position: absolute;*/
	/*top: 50%;*/
	/*left: 0;*/
	/*transform: translate(-50%, -50%);*/
	width: 30ch;
	padding: 0.5em;
	
	background: var(--main-background);
	border: 1px solid var(--main-color);
	box-shadow: var(--box-shadow-focus);
	z-index: 4;
}

.popup-box-enter-active {
	transition: 
		opacity 120ms ease-out, 
		transform 50ms ease-out;
}
.popup-box-leave-active {
	transition: 
		opacity 50ms ease-in, 
		transform 120ms ease-out;
}
.popup-box-enter,
.popup-box-leave-to {
	transform: translateY(-20%) scaleY(0.7);
	/*transform-origin: top;*/
	opacity: 0;
}

</style>